import React, {useState, useEffect} from 'react';

//Firebase stuff
import 'firebase/firestore';
import { getAuth } from "firebase/auth";
import { useAuthState } from 'react-firebase-hooks/auth';
import { getFirestore, collection, onSnapshot } from "firebase/firestore";
import firebase from "./firebase";

import "./style/nav.css";

//views
import Content from "./views/Content";
import SignInPage from './views/SignInPage';
import Setup from './views/Setup';

//css
import 'bootstrap/dist/css/bootstrap.min.css';

export default function App() {
  const [user] = useAuthState(getAuth());
  const [users, setUsers] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const ref = collection( getFirestore(), "users" );

  useEffect(() => {
    //lai parbauditu vai lietotƒÅjs ir aktƒ´vs, proti vai ir iziets Setup ekrƒÅns
    setIsLoading(true);
    onSnapshot(ref, (item) => {
      const items = [];
      item.forEach((doc) => {
          items.push({
              _id: doc.id,
              uid: doc.data().uid,
              active: doc.data().active
          });
      })
      setUsers(items);
      setIsLoading(false);
  });
  }, []);

  return (
    <div>
      {!isLoading ? (!user ? <SignInPage /> : (users.some(e => e.uid === user.uid) && users.find(e => e.uid === user.uid).active ? <Content /> : <Setup />)): null}
    </div>
  );
}
