import React, {useState, useEffect} from 'react';
//lai authentificƒìties ar google
import { getAuth, signInWithPopup, GoogleAuthProvider, signInWithRedirect } from "firebase/auth";
export default function SignInPage() {

    const signInWithGoogle = () => {
      //pievienoties ar google profilu
        const provider = new GoogleAuthProvider();
        signInWithRedirect(getAuth(), provider);
        console.log(getAuth());
      }
    
      return (
        <div style={{paddingTop: 200}} className="input-center">
          <h1 style={{width: 50 + '%',fontWeight: 'bold', textAlign: 'center', paddingBottom: 40}}>Healthy lifestyle management</h1>
          <button className="btn btn-primary" onClick={signInWithGoogle}> Sign in with Google </button>
        </div>
      );
}