import React, {useState} from 'react';
//darbam ar datu bazi
import { getFirestore, collection, addDoc } from "firebase/firestore";
import FoodList from './foodList';

export default function AddNutrition() {  
  
    const [success, setSuccess] = useState(null);
    const [food, setFood] = useState({
        carbs: 0,
        fat: 0,
        kcal: 0,
        name: '',
        protein: 0,
    });

    const addFood = () => {
      //pievieno jaunu edienu, edienu tabulai
      if(food.carbs !== 0 && food.fat !== 0 && food.kcal !== 0 && food.name !== '' && food.protein !== 0){
        addDoc(collection(getFirestore(), "nutrition"), {
          carbs: food.carbs,
          fat: food.fat,
          kcal: food.kcal,
          name: food.name,
          protein: food.protein,
        });
        setSuccess(true)
      }else{
        setSuccess(false)
      }
    }

      return (
        <div className="center-content">
          <div className="user-info">
              <h3>Add food</h3>
            <div className="user-info-row">
              <h6>Name</h6>
              <input placeholder="Name"
                        onChange={num => setFood(e => ({
                            ...e,
                            name: num.target.value,
                        }))}></input>
            </div>
            <div className="user-info-row">
              <h6>Carbs</h6>
              <input type="number" placeholder="Carbs"
                        onChange={num => setFood(e => ({
                            ...e,
                            carbs: num.target.value,
                        }))}></input>
            </div>            
            <div className="user-info-row">
              <h6>Protein</h6>
              <input type="number" placeholder="Protein"
                        onChange={num => setFood(e => ({
                            ...e,
                            protein: num.target.value,
                        }))}></input>
            </div>            
            <div className="user-info-row">
              <h6>Fat:</h6>
              <input type="number" placeholder="Fat"
                        onChange={num => setFood(e => ({
                            ...e,
                            fat: num.target.value,
                        }))}></input>
            </div>
            <div className="user-info-row">
              <h6>Kcal</h6>
              <input type="number" placeholder="Kcal"
                        onChange={num => setFood(e => ({
                            ...e,
                            kcal: num.target.value,
                        }))}></input>
            </div>
            <button className="submit-btn" onClick={addFood}>Submit!</button>
            {success === true ? 
              (<h2 style={{color: 'greenyellow'}}>
                Success!
              </h2>)
            : success === false ? (<h2 style={{color: 'red'}}>
                Fill everything correctly
              </h2>) : null}
          </div>
        </div>
      );
}