import React, {useState, useEffect} from 'react';
//darbam ar datu bazi
import { getFirestore, collection, onSnapshot } from "firebase/firestore";

import AddDailyIntake from './addDailyIntake';
import AddNutrition from './addNutrition';

export default function FoodList(props) {
    const [food, setFood] = useState([]);
    const [isOpen, setIsOpen] = useState(false);
    const [addToDaily, setAddToDaily] = useState(false);
    const [foodId, setFoodId] = useState(0);
    const [search, setSearch] = useState('');

    const ref = collection( getFirestore(), "nutrition" );
    
    useEffect(() => {
      //datu ieg≈´≈°anai no 'nutrition' tabulas
      onSnapshot(ref, (item) => {
        const items = [];
        item.forEach((doc) => {
            items.push({
                _id: doc.id,
                carbs: doc.data().carbs,
                fat: doc.data().fat,
                kcal: doc.data().kcal,
                name: doc.data().name,
                protein: doc.data().protein,
            });
        })
        setFood(items);
    });
    }, [])

    const getFieldName = () => {
      //Atkarƒ´gs no pogas izvƒìles, kura tika nospiesta NutritionPage failƒÅ
      switch(props.field) {
        case 'breakfast':
            return 'Breakfast'
        case 'lunch':
            return 'Lunch'       
        case 'dinner':
            return 'Dinner'
        case 'extra' :
            return 'Extra'
      }
    }
//Parbauda kuru atvert 'AddDailyIntake' vai 'AddNutrition'
    const openNutrition = () => {
      if(isOpen === true) {
        setIsOpen(false);
      }else{
          setIsOpen(true);
      }
    }
    const openAddToDaily = (id) => {
      if(addToDaily === true) {
          setAddToDaily(false);
      }else{
          setAddToDaily(true);
          setFoodId(id);
      }
    }
//
    const filterFood = (value) => {
      setSearch(value)
    }
      return (
        <div className="center-content">
          {addToDaily ? <AddDailyIntake field={props.field} foodId={foodId}/> :
            (isOpen ? 
              <div>
                <AddNutrition />
                <button className="exercise-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openNutrition()}><h1>+</h1></button>
              </div> :          
              <div className='food-item'>
                <h1 style={{marginBottom: 20}}>{getFieldName()}</h1>
                <input placeholder="Search food list" style={{marginBottom: 20, marginTop: 20}} onChange={(e) => filterFood(e.target.value)}></input>
                <button className="exercise-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openNutrition()}><h1>+</h1></button>
                  {food.filter(e => e.name.indexOf(search) !== -1).map(e =>      //iet cauri visiem edieniem un attelo to        
                      <div className="object-box pointer-hover" onClick={() => openAddToDaily(e._id)}>
                        <h3>{e.name}</h3>
                        <h6>{e.kcal}kcal/100g</h6>
                      </div>
                    )}
              </div>
            )
          } 
        </div>
      );
}