import React, {useState, useEffect} from 'react';

//Darbƒ´bai ar datubƒÅzi
import { getFirestore, collection, addDoc, onSnapshot } from "firebase/firestore";

//Izmantoju auth lietotƒÅja ieg≈´≈°anƒÅ
import { getAuth } from "firebase/auth";

//tulkojumu faili, kuros tiek atlasiti visi mainigie
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';

export default function AddExerciseModal() {
    const [userData, setUserData] = useState({});
    const refUser = collection( getFirestore(), "users" );
    const [success, setSuccess] = useState(null);
    //no datubƒÅzes ieg≈´stu informƒÅciju no 'users' tabulas
    const getUserData = () => {
        onSnapshot(refUser, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    uid: doc.data().uid,
                    lan: doc.data().lan,
                });
            })
            //Tiek atrasts lietotƒÅjs salidzinot lietotƒÅja id ar authentificƒìto lietotƒÅju
            setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
          });
    }
    //Tulkojuma izvele, atkariba no datubaze lietotaja izveletƒÅ
    const getTranslation = () => {
        switch(userData.lan) {
            case 'lv':
                return lv
            case 'en':
                return en         
            case 'ru':
                return ru
            default :
                return 'undefined'
          }
    }

    //useEffect datu ieguvei
    useEffect(()=> {
        getUserData();
    }, [])

    const [name, setName] = useState('');
    const [description, setDescription] = useState('');

    const addWorkout = () => {
        //vingrinƒÅjuma pievieno≈°ana
        if(description !== '' && name !== ''){
            addDoc(collection(getFirestore(), "exerciseList"), {
                Description: description,
                Name: name
            });
            setSuccess(true);
        }else{
            setSuccess(false);
        }  
    }

    return (
        <div className="exercise-modal">
            <div className="row align-self-center">
                <div className="col">
                    <div className="row">
                        <h1>  
                            {getTranslation().addExercise}
                        </h1>
                    </div>
                    <div className="row">
                        <input className="input-element" value={name} onChange={(e) => setName(e.target.value)} placeholder={getTranslation().name}></input>
                    </div>
                    <div className="row">
                        <input className="input-element" value={description} onChange={(e) => setDescription(e.target.value)} placeholder={getTranslation().description}></input>
                    </div>
                    <div className="row input-center" style={{paddingBottom: 20}}>
                    <button title="ADD" className="btn btn-primary" style={{width: 50 + '%'}} onClick={addWorkout}>{getTranslation().add}</button>
                    {success === true ? 
                    (<h2 style={{color: 'greenyellow'}}>
                        Success!
                    </h2>)
                    : success === false ? (<h2 style={{color: 'red'}}>
                        Fill everything correctly
                    </h2>) : null}
                    </div>
                </div>
            </div>
        </div>
    );
}