import React, {useEffect, useState} from 'react';

//Darbƒ´bai ar datubƒÅzi
import { getFirestore, collection, onSnapshot, addDoc } from "firebase/firestore";
import AddExerciseModal from '../../modals/addExerciseModal';

//Izmantoju auth lietotƒÅja ieg≈´≈°anƒÅ
import { getAuth } from "firebase/auth";

//tulkojumu faili, kuros tiek atlasiti visi mainigie
import * as en from '../../translation/en';
import * as lv from '../../translation/lv';
import * as ru from '../../translation/ru';

export default function AddExerciseView() {
        const [userData, setUserData] = useState({});
        const refUser = collection( getFirestore(), "users" );
        const getUserData = () => {
        //no datubƒÅzes ieg≈´syu informƒÅciju no 'users' tabulas
            onSnapshot(refUser, (item) => {
                const items = [];
                item.forEach((doc) => {
                    items.push({
                        uid: doc.data().uid,
                        lan: doc.data().lan,
                    });
                })
                //Tiek atrasts lietotƒÅjs salidzinot lietotƒÅja id ar authentificƒìto lietotƒÅju
                setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
              });
        }
        //Tulkojuma izvele, atkariba no datubaze lietotaja izveletƒÅ
        const getTranslation = () => {
            switch(userData.lan) {
                case 'lv':
                    return lv
                case 'en':
                    return en         
                case 'ru':
                    return ru
                default :
                    return 'undefined'
              }
        }
    const [workouts, setWorkouts] = useState([]);
    const [isOpen, setIsOpen] = useState(false);
    const [defaultArray, updateArray] = useState([]);
    const [success, setSuccess] = useState(null);
    const [name, setName] = useState('');
    const [rest, setRest] = useState();
    const [sets, setSets] = useState();
    const [time, setTime] = useState();
    const [search, setSearch] = useState('');

    const ref = collection( getFirestore(), "exerciseList" );

    const getData = () => {
        //no datubƒÅzes ieg≈´ta ƒ´nformƒÅcija no tabulas 'exerciseList'
        onSnapshot(ref, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    Name: doc.data().Name,
                    Description: doc.data().Description
                });
            })
            setWorkouts(items);
        });
    }

    const openExerciseModal = () => {
        //funkcija skatƒÅs kƒÅdƒÅ stƒÅvoklƒ´ ir addExerciseModal(atverts vai ciet)
        if(isOpen === true) {
            setIsOpen(false);
        }else{
            setIsOpen(true);
        }
    }

    const checkIfAdded = (id) => {
        //Pievieno vingrinajuma id masivam
        if(defaultArray.find(el => el === id)){
            updateArray(defaultArray.filter(el => el !== id));
        }else{
            updateArray([...defaultArray, id]);
        }
        console.log(defaultArray)
    }

    const addExercise = () => {
        //Pievieno jaunu treninprogrammu datubazei
        if(name !== '' && rest > 0 && sets > 0 && time > 0 && time && sets && rest){
            addDoc(collection(getFirestore(), "workouts"), {
                Name: name,
                Rest: rest,
                Sets: sets,
                Time: time,
                Uid: getAuth().currentUser.uid,
                FriendUid: '',
                ExerciseId: defaultArray
            });
            setSuccess(true)
        }else{
            setSuccess(false);
        }
    }

    const filterExericses = (value) => {
        setSearch(value);
    }

    useEffect(() =>{
        getData();
        getUserData();
    }, []);

    return(
        <div className="container text-center">
            {isOpen ? <AddExerciseModal /> :
                <div className="row align-self-center">
                    <div className="col">
                        <h1>  
                        {getTranslation().exercises}
                        </h1>
                    </div>
                    <input className='full' placeholder="Search exercise list" style={{textAlign: 'center',marginBottom: 20, marginTop: 20}} onChange={(e) => filterExericses(e.target.value)}></input>
                    <div className="exerciseOption">
                        {//itere visus vingrinajumus, lai lietotƒÅjs varƒìtu atlasit vajadzigos trenina izpildei
                        workouts.filter(e => e.Name.indexOf(search) !== -1).map((workout) => 
                            <div className={`object-box ${defaultArray.find(el => el === workout._id) ? "selected" : null}`} onClick={() => checkIfAdded(workout._id)}>
                                <h2>{workout.Name}</h2>
                                <p>{workout.Description}</p>
                            </div>
                        )}
                    </div>
                    <div className="row">
                        <input className="input-element" value={name} onChange={(e) => setName(e.target.value)} placeholder={getTranslation().name}></input>
                    </div>
                    <div className="row">
                        <input type="number" className="input-element" value={rest} onChange={(e) => setRest(e.target.value)} placeholder={getTranslation().rest}></input>
                    </div>
                    <div className="row">
                        <input type="number" className="input-element" value={sets} onChange={(e) => setSets(e.target.value)} placeholder={getTranslation().sets}></input>
                    </div>
                    <div className="row">
                        <input type="number" className="input-element" value={time} onChange={(e) => setTime(e.target.value)} placeholder={getTranslation().time}></input>
                    </div>
                    <div className="row input-center">
                        <button type="number" className="btn btn-primary" style={{width: 50 + '%'}} onClick={addExercise}>{getTranslation().add}</button>
                    </div>
                </div> 
            }
            <button className="workout-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openExerciseModal()}><h1>+</h1></button>
            {success === true ? 
              (<h2 style={{color: 'greenyellow'}}>
                Success!
              </h2>)
            : success === false ? (<h2 style={{color: 'red'}}>
                Fill everything correctly
              </h2>) : null}
        </div>
    );
}