import React, {useEffect, useState} from 'react';
//darbam ar db
import { getFirestore, collection, onSnapshot, addDoc } from "firebase/firestore";
//auth lietotƒÅja ieg≈´≈°anai
import { getAuth } from "firebase/auth";
//tulkojuma faili
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';

export default function NewsletterPage() {
        const [userData, setUserData] = useState({});
        const [posts, setPosts] = useState([]);
        const [postText, setPostText] = useState();
        const refUser = collection( getFirestore(), "users" );
        const ref = collection( getFirestore(), "posts" );

        const getUserData = () => {
            onSnapshot(refUser, (item) => {
                const items = [];
                item.forEach((doc) => {
                    items.push({
                        uid: doc.data().uid,
                        friends: doc.data().friends,
                        lan: doc.data().lan,
                    });
                })
                setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
              });
              //postu ieg≈´≈°anai
              onSnapshot(ref, (item) => {
                  const items = [];
                  item.forEach((doc) => {
                      items.push({
                          uid: doc.data().uid,
                          text: doc.data().text,
                          likes: doc.data().likes,
                          userName: doc.data().userName,
                          photoURL: doc.data().photoURL,
                      })
                  })
                  setPosts(items);
              });
        }
        //tulkojuma iegusanai atkariba no lietotƒÅja izvƒìlƒìtƒÅ
        const getTranslation = () => {
            switch(userData.lan) {
                case 'lv':
                    return lv
                case 'en':
                    return en         
                case 'ru':
                    return ru
                default :
                    return 'undefined'
              }
        }
        useEffect(()=>{
            getUserData();
        }, [])

        const postNews = () => {
            //pieviento postu
            addDoc(collection(getFirestore(), "posts"), {
                uid: userData.uid,
                text: postText,
                likes: 0,
                userName: getAuth().currentUser.displayName,
                photoURL: getAuth().currentUser.photoURL,
            });
        }

    return(
        <div className="container text-center full">
            <div className="row align-self-center">
                <div className="col">
                    <h1>  
                    {getTranslation().newsletter}
                    </h1>
                </div>
                <div className="input-group margin-bottom">
                    <input className="news-input input-group" onChange={(e) => setPostText(e.target.value)} placeholder={getTranslation().newsletterAdd}></input>
                    <div className='input-group-append'>
                        <button className='btn btn-primary' onClick={() => postNews()}>Post</button>
                    </div>
                </div>
                {//map tikai tad ja darugu lista vai esosa ir current user uid
                posts.map(post => userData.friends.includes(post.uid) || post.uid === getAuth().currentUser.uid ? (
                    <div className='object-box post'>
                        <div>
                            <img src={post.photoURL} height={70} width={70} className="img-thumbnail align-self-center" alt="PHOTO" />
                            <p>
                                {post.userName}
                            </p>
                        </div>
                        <h5>
                            {post.text}
                        </h5>
                    </div>
                    ) : null
                )}
            </div>
        </div>
    );
}