import React, {useEffect, useState} from 'react';
//darbam ar datubazi
import { getFirestore, collection, onSnapshot, deleteDoc, doc, updateDoc } from "firebase/firestore";
import AddExerciseView from './exercise/addExercise';
import Exercise from './exercise/exercise';
//auth lietotaja ieguvei
import { getAuth } from "firebase/auth";
//tulkojumu faili
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';

export default function ExercisePage() {
    //translation
    const [userData, setUserData] = useState({});
    const refUser = collection( getFirestore(), "users" );
    const getTranslation = () => {
        switch(userData.lan) {
            case 'lv':
                return lv
            case 'en':
                return en         
            case 'ru':
                return ru
            default :
                return 'undefined'
          }
    }

    const [isOpen, setIsOpen] = useState(false);
    const [onExercise, setOnExercise] = useState(false);
    const [exerciseId, setExerciseId] = useState(0);
    const [friendData, setFriendData] = useState([{}]);
    const [friends, setFriends] = useState([]);

    const [workouts, setWorkouts] = useState([]);
    const ref = collection( getFirestore(), "workouts" );

    const getData = () => {
        //iegust datus par workouts
        onSnapshot(ref, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    ExerciseId: doc.data().ExerciseId,
                    Name: doc.data().Name,
                    Rest: doc.data().Rest,
                    Sets: doc.data().Sets,
                    Time: doc.data().Time,
                    Uid: doc.data().Uid,
                });
            })
            setWorkouts(items);
        });
        //iegust visu lietotaju datus lai iegutu eso≈°ƒÅ lietotƒÅja info un lietotƒÅja draugu masƒ´vu
        onSnapshot(refUser, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    carbs: 0,
                    protein: 0,
                    fat: 0,
                    uid: doc.data().uid,
                    gender: doc.data().gender,
                    age: doc.data().age,
                    weight: doc.data().weight,
                    height: doc.data().height,
                    activityLevel: doc.data().activityLevel,
                    active: doc.data().active,
                    calories: doc.data().calories,
                    lan: doc.data().lan,
                    friends: doc.data().friends,
                });
            })
            setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
            setFriends(items.find(e => e.uid === getAuth().currentUser.uid).friends);
          });
          //iegust visus lietotaja datus, vareju to ieprieksejƒÅ datubazes pieprasijuma izdarit
          onSnapshot(refUser, (item) => {
              const items = [];
              item.forEach((doc) => {
                  items.push({
                      _id: doc.id,
                      carbs: 0,
                      protein: 0,
                      fat: 0,
                      uid: doc.data().uid,
                      gender: doc.data().gender,
                      age: doc.data().age,
                      weight: doc.data().weight,
                      height: doc.data().height,
                      activityLevel: doc.data().activityLevel,
                      active: doc.data().active,
                      calories: doc.data().calories,
                      lan: doc.data().lan,
                      friends: doc.data().friends,
                      name: doc.data().name,
                      photo: doc.data().photo,
                  });
              })
              setFriendData(items);
            });
    }
//Atversanas funkcijas pec kuram returnƒÅ if skatas atvert exercise vai addexerciseview
    const openExerciseModal = () => {
        if(isOpen === true) {
            setIsOpen(false);
        }else{
            setIsOpen(true);
        }
    }
    const openExerciseScreen = (id) => {
        if(onExercise === true) {
            setOnExercise(false);
        }else{
            setOnExercise(true);
            setExerciseId(id);
        }
    }

    const onDelete = (id) => {
        //dzƒì≈° db ierakstu pec id
        deleteDoc(doc( getFirestore(), "workouts", id ));
        getData();
    }

    const shareWorkout = (value , id) => {
        //koplieto vingrinajumu ar draugu pievienojot drauga uid
        updateDoc(doc(getFirestore(), "workouts", id), {
            FriendUid: value,
        });
    }

    useEffect(() =>{
        getData();
    }, []);

    return(
        <div className="text-center">
            {onExercise ? <Exercise id={exerciseId} /> :
            (isOpen ? 
            <div>
                <AddExerciseView />
                <button className="exercise-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openExerciseModal()}><h1>+</h1></button>
            </div> : 
            <div>
                {workouts.map((workout) => 
                workout.Uid === getAuth().currentUser.uid ?
                <div className="object-box pointer-hover">
                    <div className='friends'>
                        <select className="select-input language" style={{marginLeft: 10, color: 'lightgray', marginTop: 0, marginBottom: 10}} onChange={(e) => shareWorkout(e.target.value, workout._id)}>
                            <option value="" disabled selected>{getTranslation().shareWith}</option>
                            {friendData.map(val => friends.includes(val.uid) ? (
                                <option value={val.uid}>{val.name}</option>
                            ): null)}
                        </select>
                        <button className="remove-button" onClick={() => onDelete(workout._id)}>x</button>
                    </div>
                    <h2>{workout.Name}</h2>
                    <div className="d-flex workout-info"  onClick={() => openExerciseScreen(workout._id)}>
                        <p>{getTranslation().restTime}: {workout.Rest} min</p>
                        <p>{getTranslation().sets}: {workout.Sets}</p>
                        <p>{getTranslation().time}: {//lai iegutu korektu laiku no visam rest sets time vertibam
                        (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)) > 59 ?
                         ((Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/(60*60))) < 10 ?
                          '0' + (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/(60*60))) :
                          (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/(60*60))))
                         + ':' + ((Math.floor(((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))/60))) < 10 ?
                         '0' + (Math.floor(((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))/60))) : 
                         (Math.floor(((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))/60))))
                         + ':' + (((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))%60) < 10 ?
                         '0' + ((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))%60) :
                         ((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))%60))
                         :    
                        (((Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)) < 10 ?
                        '0' + (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)) :
                        (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)))
                         + ':' + 
                         (((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%60 < 10 ?
                         '0' + ((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%60 :
                         ((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%60))}
                          </p>
                    </div>
                </div> : null
                )}
                <button className="exercise-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openExerciseModal()}><h1>+</h1></button>
            </div>
            )}
        </div>
    );
}