import React, {useState,useEffect} from 'react';
//auth lietotƒÅja ieg≈´≈°anai
import { getAuth } from "firebase/auth";
import { useAuthState } from 'react-firebase-hooks/auth';
//darbam ar db
import { getFirestore, collection, addDoc } from "firebase/firestore";

export default function Setup() {
    const [success, setSuccess] = useState(null);
    const [user] = useAuthState(getAuth());
    const [userData, setUserData] = useState({
        uid: user.uid,
        gender: '',
        age: '',
        weight: '',
        height: '',
        activityLevel: '',
    });

    const addData = () => {
        //pievienot visu user info users tabulai
        if(userData.gender !== '' && userData.age > 0 && userData.weight > 0 && userData.height > 0 && userData.activityLevel !== ''){
            addDoc(collection(getFirestore(), "users"), {
                uid: userData.uid,
                gender: userData.gender,
                age: userData.age,
                weight: userData.weight,
                height: userData.height,
                activityLevel: userData.activityLevel,
                lan: 'lv',
                active: true,
                friends: [],
                name: getAuth().currentUser.displayName,
                photo: getAuth().currentUser.photoURL,
            });
            setSuccess(true);
        }else{
            setSuccess(false);
        }
    }

    useEffect(() => {
        console.log(userData)
    }, [userData])

      return (
        <div className="center-content">
            Hi {user.displayName}!
            Tell me about yourself!
            <div className="user-info">
                <div className="user-info-row">
                    <h5>
                        Gender
                    </h5>
                    <div>
                        <button onClick={() => setUserData(e => ({
                            ...e,
                            gender: 'male',
                        }))}
                        className={`setup-btn ${userData.gender === 'male' ? 'btn-active' : null}`}>
                            Male
                        </button>
                        <button onClick={() => setUserData(e => ({
                            ...e,
                            gender: 'female',
                        }))}
                        className={`setup-btn ${userData.gender === 'female' ? 'btn-active' : null}`}>
                            Female
                        </button>
                    </div>
                </div>
                <div className="user-info-row">
                    <h5 className="align-center">
                        Age
                    </h5>
                    <div>
                        <input type="number" placeholder="Age" 
                        onChange={num => setUserData(e => ({
                            ...e,
                            age: num.target.value,
                        }))}>
                        </input>
                    </div>
                </div>
                <div className="user-info-row">
                    <h5 className="align-center">
                        Weight
                    </h5>
                    <div>
                        <input type="number" placeholder="Weight"                        
                        onChange={num => setUserData(e => ({
                            ...e,
                            weight: num.target.value,
                        }))}></input>
                    </div>
                </div>
                <div className="user-info-row">
                <h5 className="align-center">
                        Height
                    </h5>
                    <div>   
                        <input type="number" placeholder="Height"
                        onChange={num => setUserData(e => ({
                            ...e,
                            height: num.target.value,
                        }))}></input>
                    </div>
                </div>
                <div className="user-info-row">
                <h5 className="align-center">
                        Activity level
                    </h5>
                    <div>
                        <select className="select-input" 
                        onChange={num => setUserData(e => ({
                            ...e,
                            activityLevel: num.target.value,
                        }))}>
                            <option value="" disabled selected>
                                Choose activity level
                            </option>
                            <option value="sedentary">Sedentary</option>
                            <option value="lactive">Lightly Active(1-2 days)</option>
                            <option value="mactive">Moderately Active(3-5 days)</option>
                            <option value="vactive">Very Active(6-7 days)</option>
                            <option value="eactive">Extremely Active</option>
                        </select>
                    </div>
                </div>
                <button className="submit-btn" onClick={addData}>Finish!</button>
                {success === true ? 
                (<h2 style={{color: 'greenyellow'}}>
                    Success!
                </h2>)
                : success === false ? (<h2 style={{color: 'red'}}>
                    Fill everything correctly
                </h2>) : null}
            </div>
        </div>
      );
}