import React, {useState, useEffect} from 'react';
//darbibai ar datu bazi
import { getFirestore, collection, onSnapshot, doc, updateDoc} from "firebase/firestore";
//authintifecta lietotaja iegusanai, tikai tam so izmantoju
import { getAuth } from "firebase/auth";

export default function AddFriends() {
    const ref = collection( getFirestore(), "users" );
    const [userData, setUserData] = useState([]);
    const [startedSearch, setStartedSearch] = useState(false);
    const [current, setCurrent] = useState({});
    const [search, setSearch] = useState('');
    const [array, setArray] = useState([]);

    useEffect(() => {
        //iegust users informaciju no users tabulas
        onSnapshot(ref, (item) => {
          const items = [];
          item.forEach((doc) => {
              items.push({
                _id: doc.id,
                carbs: 0,
                protein: 0,
                fat: 0,
                uid: doc.data().uid,
                gender: doc.data().gender,
                age: doc.data().age,
                weight: doc.data().weight,
                height: doc.data().height,
                activityLevel: doc.data().activityLevel,
                active: doc.data().active,
                calories: doc.data().calories,
                lan: doc.data().lan,
                friends: doc.data().friends,
                name: doc.data().name,
                photo: doc.data().photo,
              });
          })
          setUserData(items);
          //iegust current user salidzinot ielogota uid ar users tabulas lietotaju uid
          setCurrent(items.find(e => e.uid === getAuth().currentUser.uid));
        });
    }, [array])

    const filterUsers = (value) => {
        //lai neraditos visi useri kad nav nekas rakstits, un state saglabƒÅ ierakstito
        setSearch(value);
        if(search === ''){
            setStartedSearch(false);
        }else{
            setStartedSearch(true);
        }
    }

    const addFriend = (value) => {
        //uzspiezot pievinot, atjauno friends sarakstu tabulƒÅ
        setArray(userData.find(e => e.uid === getAuth().currentUser.uid).friends);
        setArray(e => [...e, value])
        updateDoc(doc(getFirestore(), "users", userData.find(e => e.uid === getAuth().currentUser.uid)._id), {
            friends: array,
        });
        console.log(array)
    }

    return (
        <div className='input-center'>
            <input placeholder="Search users" onChange={(e) => filterUsers(e.target.value)}></input>
            {startedSearch ? 
                userData.filter(e => e.name.indexOf(search) !== -1).map(e => {
                    return(
                    <div className='setting-buttons friends'>
                        <div className='d-flex'>
                            <img src={e.photo} height={70} width={70} className="img-thumbnail align-self-center" alt="PHOTO" />
                            <h2 style={{paddingLeft: 10}}>{e.name}</h2>
                        </div>
                        <button className='btn btn-primary' disabled={current.friends.includes(e.uid)} onClick={() => addFriend(e.uid)}>Follow</button>
                    </div>
                    )
                }):null
            }
        </div>
    );
}