import React, {useEffect, useState} from 'react';
//auth lietotƒÅja ieguvei
import { getAuth } from "firebase/auth";
//tulkojuma faili
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';
//skatu faili
import ExercisePage from './exercisePage';
import NutritionPage from './nutritionPage';
import SharedPage from './sharedPage';
//darbam ar db
import { getFirestore, collection, onSnapshot } from "firebase/firestore";
//ikonas faili
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDumbbell, faRetweet, faUtensils } from "@fortawesome/free-solid-svg-icons";

export default function HomePage() {
    const [Active, setActive] = useState("none");
    const [userData, setUserData] = useState({});
    const [workouts, setWorkouts] = useState([]);
    const [recent, setRecent] = useState({});

    const refWorkouts = collection( getFirestore(), "workouts" );
    const refRecent = collection( getFirestore(), "recentWorkouts" );
    const refUser = collection( getFirestore(), "users" );

    const getCorrespondingPage = (value) => {
        switch(value) {
            case 'exercise':
                return <ExercisePage />
            case 'nutrition':
                return <NutritionPage />
            case 'shared':
                return <SharedPage />
          }
    }
    //translacijas ieg≈´≈°anai
    const getTranslation = () => {
        switch(userData.lan) {
            case 'lv':
                return lv
            case 'en':
                return en         
            case 'ru':
                return ru
            default :
                return 'undefined'
          }
    }

    useEffect(() => {
        setActive('none');
        //recent ekraninam
        onSnapshot(refRecent, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    workoutId: doc.data().workoutId,
                    uid: doc.data().uid,
                    date: doc.data().date
                });
            })
            setRecent(items ? items[items.length-1] : null);
        });
        //recent ekraninam
        onSnapshot(refWorkouts, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    ExerciseId: doc.data().ExerciseId,
                    Name: doc.data().Name,
                    Rest: doc.data().Rest,
                    Sets: doc.data().Sets,
                    Time: doc.data().Time
                });
            })
            
            setWorkouts(items);
        });
        //valodai
        onSnapshot(refUser, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    uid: doc.data().uid,
                    lan: doc.data().lan,
                });
            })
            setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
          });
    }, []);

    return(
        <div className="container text-center">
            {Active !== 'none' ? getCorrespondingPage(Active) :
            <div> 
                <div className="banner d-flex center">
                    <h1>  
                    {getTranslation().welcomeBack}&nbsp;
                    </h1> 
                    <h1 style={{color: 'blueviolet'}}>
                    {getAuth().currentUser.displayName}
                    </h1>
                    <h1>
                        !
                    </h1>
                </div>
                <div className="containers">
                    <div className="home-objects" onClick={() => setActive('exercise')}>
                        <FontAwesomeIcon color='greenyellow' size="3x" icon={faDumbbell} />
                        <h1 style={{paddingTop: 20}}>{getTranslation().workouts}</h1>
                    </div>
                    <div className="home-objects" onClick={() => setActive('nutrition')}>       
                        <FontAwesomeIcon color='lightblue' size="3x" icon={faUtensils} />
                        <h1 style={{paddingTop: 20}}>{getTranslation().nutrition}</h1>
                    </div>
                    <div className="home-objects" onClick={() => setActive('shared')}>
                        <FontAwesomeIcon color='pink' size="3x" icon={faRetweet} />
                        <h1 style={{paddingTop: 20}}>{getTranslation().shared}</h1>
                    </div>
                    <div className="home-recent">
                        <h2>{getTranslation().recent}</h2>
                        {workouts.map((workout) => {
                            if(recent){
                            if(workout._id === recent.workoutId && recent.uid === getAuth().currentUser.uid){
                            return(
                            <div className="object-box recent">
                                <div>
                                    <h1>
                                        {workout.Name}
                                    </h1>
                                    <p>
                                        {getTranslation().time}:
                                        {//laika pareizai attaino≈°anai
                                        (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)) > 59 ?
                                        ((Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/(60*60))) < 10 ?
                                        '0' + (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/(60*60))) :
                                        (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/(60*60))))
                                        + ':' + ((Math.floor(((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))/60))) < 10 ?
                                        '0' + (Math.floor(((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))/60))) : 
                                        (Math.floor(((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))/60))))
                                        + ':' + (((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))%60) < 10 ?
                                        '0' + ((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))%60) :
                                        ((((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%(60*60))%60))
                                        :    
                                        (((Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)) < 10 ?
                                        '0' + (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)) :
                                        (Math.floor(((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)/60)))
                                        + ':' + 
                                        (((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%60 < 10 ?
                                        '0' + ((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%60 :
                                        ((workout.Time*60+workout.Rest*60)*workout.Sets*workout.ExerciseId.length)%60))}
                                    </p>
                                </div>
                                <p style={{color: 'lightgray'}}>
                                    {recent.date}
                                </p>
                            </div>
                            )}
                        }})}
                    </div>
                </div>
            </div>
            }   
        </div>
    );
}