import React, {useState, useEffect} from 'react';

import FriendsList from './friendsList';
import ChangeUserInfo from './changeUserInfo';
//auth lietotƒÅja informƒÅciijas ieg≈´≈°anai
import { getAuth } from "firebase/auth";
//darbam ar db
import { getFirestore, doc, collection, updateDoc, onSnapshot} from "firebase/firestore";
//tulkojuma faili
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';

export default function ProfilePage() {
  //tulkojumam
  const [uData, setUData] = useState({});
  const refUser = collection( getFirestore(), "users" );
  const getUserData = () => {
      onSnapshot(refUser, (item) => {
          const items = [];
          item.forEach((doc) => {
              items.push({
                  uid: doc.data().uid,
                  lan: doc.data().lan,
              });
          })
          setUData(items.find(e => e.uid === getAuth().currentUser.uid));
        });
  }
  const getTranslation = () => {
      switch(uData.lan) {
          case 'lv':
              return lv
          case 'en':
              return en         
          case 'ru':
              return ru
          default :
              return 'undefined'
        }
  }
  useEffect(()=>{
      getUserData();
  }, [])
//
  const ref = collection( getFirestore(), "users" );
  const [userData, setUserData] = useState({});
  const [friendsOpen, setFriendsOpen] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    onSnapshot(ref, (item) => {
      const items = [];
      item.forEach((doc) => {
          items.push({
              _id: doc.id,
              carbs: 0,
              protein: 0,
              fat: 0,
              uid: doc.data().uid,
              gender: doc.data().gender,
              age: doc.data().age,
              weight: doc.data().weight,
              height: doc.data().height,
              activityLevel: doc.data().activityLevel,
              active: doc.data().active,
              calories: doc.data().calories,
              lan: doc.data().lan,
          });
      })
      //auth lietotƒÅja ieg≈´≈°anai
      setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
    });
  }, [])

  const changeLanguage = (value) => {
    //valodas mainƒ´≈°anai
      updateDoc(doc(getFirestore(), "users", userData._id), {
          lan: value,
      });
  }
    return (
      <div>
        {!friendsOpen ? 
        !isOpen ? (
          <div>
            <div style={{paddingTop: "10px"}} className="d-flex flex-row">      
              <img src={getAuth().currentUser.photoURL}  height={100} width={100} className="img-thumbnail align-self-center" alt="PHOTO" />
              <div style={{paddingLeft: "5px"}}>
                <h2>{getAuth().currentUser.displayName}</h2>
                <h2>{getAuth().currentUser.email}</h2>
              </div>
              <button style={{marginLeft: "auto"}} className="btn btn-sm btn-primary align-self-baseline" onClick={() => getAuth().signOut()}> {getTranslation().signOut} </button>
            </div>
            <div>
              <select className="select-input language" 
              onChange={num => changeLanguage(num.target.value)}>
                  <option value="" disabled selected>
                    {getTranslation().changeLanguage}
                  </option>
                  <option value="lv">LV</option>
                  <option value="en">EN</option>
                  <option value="ru">RU</option>
              </select>
            </div>
            <div className='setting-buttons' onClick={() => setIsOpen(true)}>
                <h2>
                  {getTranslation().userInfo}
                </h2>
            </div>
            <div className='setting-buttons' onClick={() => setFriendsOpen(true)}>
                <h2>
                  {getTranslation().friends}
                </h2>
            </div>
          </div>
        ): <ChangeUserInfo /> : <FriendsList />}
      </div>
    );
  }