import React, {useState, useEffect} from 'react';
//authentificeta lietotaja ieguvei
import { getAuth } from "firebase/auth";
//darbam ar db
import { getFirestore, collection, onSnapshot, updateDoc, doc} from "firebase/firestore";

import AddFriends from "./addFriends";

export default function FriendsList() {
  const ref = collection( getFirestore(), "users" );
  const [userData, setUserData] = useState({});
  const [friendData, setFriendData] = useState([]);
  const [isOpen, setIsOpen] = useState(false);

    useEffect(() => {
        //lietotƒÅju ieguvei, draugiem un lietotaja info, vareju to visu viena pieprasijuma, bet rakstot bija saprotamak divos
        onSnapshot(ref, (item) => {
          const items = [];
          item.forEach((doc) => {
              items.push({
                  _id: doc.id,
                  carbs: 0,
                  protein: 0,
                  fat: 0,
                  uid: doc.data().uid,
                  gender: doc.data().gender,
                  age: doc.data().age,
                  weight: doc.data().weight,
                  height: doc.data().height,
                  activityLevel: doc.data().activityLevel,
                  active: doc.data().active,
                  calories: doc.data().calories,
                  lan: doc.data().lan,
                  friends: doc.data().friends,
              });
          })
          setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
        });
        onSnapshot(ref, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    carbs: 0,
                    protein: 0,
                    fat: 0,
                    uid: doc.data().uid,
                    gender: doc.data().gender,
                    age: doc.data().age,
                    weight: doc.data().weight,
                    height: doc.data().height,
                    activityLevel: doc.data().activityLevel,
                    active: doc.data().active,
                    calories: doc.data().calories,
                    lan: doc.data().lan,
                    friends: doc.data().friends,
                    name: doc.data().name,
                    photo: doc.data().photo,
                });
            })
            setFriendData(items);
          });
      }, [])

      //lai atvertu draugu meklesanas logu
      const openFriendsSearch = () => {
        if(isOpen === true) {
          setIsOpen(false);
        }else{
            setIsOpen(true);
        }
      }

      const removeFriend = (friend) => {
          //drauga iznemsanai no users tabulas
          console.log(userData.friends.filter(e => e !== friend))
          console.log(friend)
        updateDoc(doc(getFirestore(), "users", userData._id), {
            friends: userData.friends.filter(e => e !== friend),
        });
      }

      return (
        <div className="full">
            {isOpen ? 
            <div>
                <AddFriends />
                <button className="exercise-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openFriendsSearch()}><h1>+</h1></button>
              </div> :  (
            <div>
            <h1>
                Friends list
            </h1>
            {userData.friends && friendData ? userData.friends.map(friend => friendData.find(e => e.uid === friend) ? 
                <div className='setting-buttons friends'>
                    <div className='d-flex' >
                        <img src={friendData.find(e => e.uid === friend).photo} height={70} width={70} className="img-thumbnail align-self-center" alt="PHOTO" />
                        <h2 style={{paddingLeft: 10}}>{friendData.find(e => e.uid === friend).name}</h2>
                    </div>
                    <button className='btn btn-primary' onClick={() => removeFriend(friend)}>Unfollow</button>
                </div>
            :null ): null}
            <button className="exercise-add-button" style={isOpen ?  {transform: 'rotate(45deg)'} :  null} onClick={() => openFriendsSearch()}><h1>+</h1></button>
            </div>)}
        </div>
      );
}