import React, {useState, useEffect} from 'react';
//darba, ar db
import { getFirestore, collection, onSnapshot, addDoc } from "firebase/firestore";
//auth lietotƒÅja ieguvei
import { getAuth } from "firebase/auth";
import { useAuthState } from 'react-firebase-hooks/auth';
//ikonu izmanto≈°anai
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSun, faDrumstickBite, faAppleAlt, faFish} from "@fortawesome/free-solid-svg-icons";

import FoodList from './nutrition/foodList';
//tulkojumu faili
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';

export default function NutritionPage() {
        //sadaƒºa lai tiktu tulkots
        const [uData, setUData] = useState({});
        const refUser = collection( getFirestore(), "users" );
        const getUserData = () => {
            onSnapshot(refUser, (item) => {
                const items = [];
                item.forEach((doc) => {
                    items.push({
                        uid: doc.data().uid,
                        lan: doc.data().lan,
                    });
                })
                setUData(items.find(e => e.uid === getAuth().currentUser.uid));
              });
        }
        const getTranslation = () => {
            switch(uData.lan) {
                case 'lv':
                    return lv
                case 'en':
                    return en         
                case 'ru':
                    return ru
                default :
                    return 'undefined'
              }
        }
        useEffect(()=>{
            getUserData();
        }, [])
        //
    const [userData, setUserData] = useState({});
    const [isOpen, setIsOpen] = useState(false);
    const [field, setField] = useState('');
    const [user] = useAuthState(getAuth());
    const [kcalIntake, setKcalIntake] = useState([]);
    const [isLoaded, setIsLoaded] = useState(false);

    const ref = collection( getFirestore(), "users" );
    const refDaily = collection( getFirestore(), "dailyIntake");

    useEffect(() => {
        setIsLoaded(false);
        //iegust dailyIntake informaciju
        onSnapshot(refDaily, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    uid: doc.data().uid,
                    carbs: doc.data().carbs,
                    date: doc.data().date,
                    fat: doc.data().fat,
                    field: doc.data().field,
                    grams: doc.data().grams,
                    kcal: doc.data().kcal,
                    name: doc.data().name,
                    protein: doc.data().protein,
                });
            })
            setKcalIntake(items.filter(e => e.uid === user.uid));
            setKcalIntake(kcalIntake.filter(value => value.date === Date(Date.now()).toString().split(' ').slice(0, 4).join(' ')))
        });
        //lietoƒÅja informƒÅcijas iegusanai
        onSnapshot(ref, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    carbs: 0,
                    protein: 0,
                    fat: 0,
                    uid: doc.data().uid,
                    gender: doc.data().gender,
                    age: doc.data().age,
                    weight: doc.data().weight,
                    height: doc.data().height,
                    activityLevel: doc.data().activityLevel,
                    active: doc.data().active,
                    calories: Math.floor((((10*doc.data().weight)+(6.25*doc.data().height)-(5*doc.data().age)+5)*1.1)*getActivityValue(doc.data().activityLevel)),
                });
            })
            setUserData(items.find(e => e.uid === user.uid));
            kcalIntake.forEach(e => {
                if(e.date === Date(Date.now()).toString().split(' ').slice(0, 4).join(' ')){
                    //ja lauka datums ir  === sodienas datums, tad visas kalorijas +
                    setUserData(value => ({
                        ...value,
                        calories: value.calories-e.kcal,
                        carbs: value.carbs + e.carbs,
                        protein: value.protein + e.protein,
                        fat: value.fat + e.fat,
                    }));
                }
            })
        });
        setIsLoaded(true);
    }, [kcalIntake])

    //atverts vai aizverts pievieno≈°anas ekrans
    const openAddPage = (value) => {
        if(isOpen === true) {
            setIsOpen(false);
        }else{
            setIsOpen(true);
            setField(value);
        }
    }

    const getActivityValue = (value) => {
        //vajadzigs lai noteiktu korekto cal daudzumu
        switch(value) {
            case 'sedentary':
                return 1.2
            case 'lactive':
                return 1.375         
            case 'mactive':
                return 1.55
            case 'vactive':
                return 1.725 
            case 'eactive':
                return 1.9
          }
    }

    const getKcal = (value) => {
        //kcal katrai diena dala iegusanai
        var kcal = 0;
        kcalIntake.forEach(e => {
            if(value === e.field){
                kcal = kcal + e.kcal;
            }
        })
        return kcal;
    }

    return(
        <div>
        {isOpen ? <FoodList field={field}/> :
        <div className="center-content">
            <p>{Math.floor((((10*userData.weight)+(6.25*userData.height)-(5*userData.age)+5)*1.1)*getActivityValue(userData.activityLevel)) - userData.calories} eaten</p>
            <div className="kcal-circle">
                <h1>{isLoaded ? userData.calories : null}</h1>
                <p>{getTranslation().kcalLeft}</p>
            </div>
            <div className="nutrition-info">
                <div>
                    <p className='macros'>
                        {getTranslation().carbs}
                    </p>
                    <p className='macros'>{isLoaded ? userData.carbs : '0'}/{isLoaded ? Math.floor((userData.calories*0.5)/4) : '0'}g</p>
                </div>
                <div>
                    <p className='macros'>
                        {getTranslation().protein}
                    </p>
                    <p className='macros'>{isLoaded ? userData.protein : '0'}/{isLoaded ? Math.floor((userData.calories*0.25)/4) : '0'}g</p>
                </div>
                <div>
                    <p className='macros'>
                        {getTranslation().fat}
                    </p>
                    <p className='macros'>{isLoaded ? userData.fat : '0'}/{isLoaded ? Math.floor((userData.calories*0.25)/9) : '0'}g</p>
                </div>
            </div>
            <p>{Date(Date.now()).toString().split(' ').slice(0, 4).join(' ')}</p>
            <div className='user-info'>
               <div className='daily-intake-box' onClick={() => openAddPage('breakfast')} >
                   <FontAwesomeIcon className='icon-padding' size="lg" icon={faSun} />
                   <h3>
                       {getTranslation().addBreakfast}
                   </h3>
                   <p className='kcal-info'>
                    {getKcal('breakfast')} kcal
                   </p>
                </div> 
                <div className='daily-intake-box' onClick={() => openAddPage('lunch')} >
                    <FontAwesomeIcon className='icon-padding' size="lg" icon={faFish} />
                    <h3>
                        {getTranslation().addLunch}
                    </h3>
                    <p className='kcal-info'>
                    {getKcal('lunch')} kcal
                   </p>
                </div>
                <div className='daily-intake-box' onClick={() => openAddPage('dinner')} >
                    <FontAwesomeIcon className='icon-padding' size="lg" icon={faDrumstickBite} />
                    <h3>
                        {getTranslation().addDinner}
                    </h3>
                    <p className='kcal-info'>
                    {getKcal('dinner')} kcal
                   </p>
                </div>
                <div className='daily-intake-box' onClick={() => openAddPage('extra')} >
                    <FontAwesomeIcon className='icon-padding' size="lg" icon={faAppleAlt} />
                    <h3>
                        {getTranslation().addExtra}
                    </h3>
                    <p className='kcal-info'> 
                    {getKcal('extra')} kcal
                   </p>
                </div>
            </div>
            </div>
        }
        </div>
    );
}