import React, {useState, useEffect} from 'react';
//auth lietaja info ieguvei
import { getAuth } from "firebase/auth";
import { useAuthState } from 'react-firebase-hooks/auth';
//darbam ar db
import { getFirestore, collection, onSnapshot, addDoc } from "firebase/firestore";

export default function AddDailyIntake(props) {
    const [user] = useAuthState(getAuth());    
    const [success, setSuccess] = useState(null);
    const [food, setFood] = useState({
      _id: '',
      carbs: 0,
      fat: 0,
      kcal: 0,
      name: '',
      protein: 0,
    });
    const [grams, setGrams] = useState(0);

    const ref = collection( getFirestore(), "nutrition" );
    
    useEffect(() => {
      //tabulas 'nutrition' datu ieguvei
      onSnapshot(ref, (item) => {
        const items = [];
        item.forEach((doc) => {
            items.push({
                _id: doc.id,
                carbs: doc.data().carbs,
                fat: doc.data().fat,
                kcal: doc.data().kcal,
                name: doc.data().name,
                protein: doc.data().protein,
            });
        })
        setFood(items.find(e => e._id === props.foodId));
    });
    }, [])

    const addFood = () => {
      //pievieno edienu 'dailyIntake tabulai'
      if(grams !== 0 && grams !== undefined){
        addDoc(collection(getFirestore(), "dailyIntake"), {
          uid: user.uid,
          date: Date(Date.now()).toString().split(' ').slice(0, 4).join(' '),//Date sadala isaka, lai butu tikai vajadziga informacija
          name: food.name,
          carbs: food.carbs*(grams/100),
          fat: food.fat*(grams/100),
          protein: food.protein*(grams/100),
          kcal: food.kcal*(grams/100),
          grams: grams,
          field: props.field,
      });
      setSuccess(true);
      }else{
        setSuccess(false);
      }
    }

      return (
        <div className="center-content">
          <div className="user-info">
            <div className="user-info-row">
              <h3>{food.name}|{food.kcal}kcal</h3>
            </div>
            <div className="user-info-row">
              <h6>Carbs: {food.carbs}</h6>
            </div>            
            <div className="user-info-row">
              <h6>Protein: {food.protein}</h6>
            </div>            
            <div className="user-info-row">
              <h6>Fat: {food.fat}</h6>
            </div>
            <div className="user-info-row">
              <h6>Grams</h6>
              <input type="number" placeholder="Grams" onChange={num => setGrams(num.target.value)}></input>
            </div>
            <button className="submit-btn" onClick={addFood}>Submit!</button>
            <div>
            {success === true ? 
              (<h2 style={{color: 'greenyellow'}}>
                Success!
              </h2>)
            : success === false ? (<h2 style={{color: 'red'}}>
                Fill everything correctly
              </h2>) : null}
            </div>
          </div>
        </div>
      );
}