import React, {useEffect, useState} from 'react';
//darbam ar datubƒÅzi
import { getFirestore, collection, onSnapshot, addDoc } from "firebase/firestore";

//auth lietotƒÅja ieg≈´≈°anai
import { getAuth } from "firebase/auth";

//ikonu ieg≈´≈°anai
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPause, faPlay } from "@fortawesome/free-solid-svg-icons";

export default function Exercise(id) {
    
    const [timer, setTimer] = useState();
    const [workouts, setWorkouts] = useState([]);
    const [exerciseInfo, setExerciseInfo] = useState({});
    const [exerciseIds, setExerciseIds] = useState([]);
    const [exerciseStatus, setExerciseStatus] = useState('');
    const [workoutStatus, setWorkoutStatus] = useState({
        Running: '',
        Order: 0,
        Done: [],
    });
    const refWorkouts = collection( getFirestore(), "workouts" );
    const refExercises = collection( getFirestore(), "exerciseList" );
    const [complete, setComplete] = useState(false);
    
    //sakot programmu datu ieguvei
    useEffect(() => {
        getAllWorkouts();
        getExerciseData();
    }, []);

    useEffect(() => {
        //skatamies pec statusa ko sakt treninu vai atputu
        if(exerciseInfo && exerciseInfo.Time < 0 && (exerciseInfo.Sets !== 1 || !exerciseInfo.Last)){
            if(exerciseStatus === "exercise"){
                startRest();
            }
            if(exerciseStatus === "rest"){
                startExercise();
            }
        }
        //Ja laiks beidzies pievieno informaciju par pabeigto vingrinajumu un sƒÅk nƒÅkamo taimeri
        if(exerciseInfo.Time === 0 && exerciseInfo.Last){
            clearInterval(timer);
            console.log(timer);
            workoutStatus.Done.push(workoutStatus.Running);
            if(exerciseIds.length !== workoutStatus.Order+1){
                setWorkoutStatus(e => ({
                    ...e,
                    Running: exerciseIds[e.Order+1],
                    Order: e.Order+1,
                }));
                getExerciseData();
                startExercise();
            }else{
                addDoc(collection(getFirestore(), "recentWorkouts"), {
                    workoutId: exerciseInfo._id,
                    date: Date(Date.now()).toString().split(' ').slice(0, 4).join(' '),
                    uid: getAuth().currentUser.uid,
                });
                setComplete(true);
            }
        }
    }, [exerciseInfo])

    const getAllWorkouts = () => {
        //iegust informaciju  no 'exerciseList'
        onSnapshot(refExercises, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    Name: doc.data().Name,
                    Description: doc.data().Description
                });
            })
            setWorkouts(items);
        });
    }

    const getExerciseData = () => {
        //iegust informaciju no 'workouts'
        onSnapshot(refWorkouts, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    _id: doc.id,
                    Name: doc.data().Name,
                    Rest: doc.data().Rest,
                    Sets: doc.data().Sets,
                    Time: doc.data().Time,
                    ExerciseId: doc.data().ExerciseId
                });
            })
            if(items){
                setExerciseIds(items.find((e) => e._id === id.id).ExerciseId);
            }
            setExerciseInfo(items.find((e) => e._id === id.id));
            //laiku parver≈°s sekundƒìs
            setExerciseInfo(e => ({
                ...e,
                Rest: e.Rest*60,
                Time: e.Time*60,
                Exercise: e.Time*60
            }))
        });
    }

    const startExercise = () => {
        //maina time informƒÅciju uz vingrinajuma izpildijuma paradzetoo, tagad mainoties exerciseInfo.Time, to pƒÅrbauda useEffect
        setWorkoutStatus(e => ({
            ...e,
            Running: exerciseIds[e.Order],
        }));
        clearInterval(timer);
        setTimer(undefined);
        setExerciseStatus("exercise");
        setExerciseInfo(e => ({
            ...e,
            Time: e.Exercise
        }));
        if(exerciseInfo.Sets === 1){
            setExerciseInfo(e => ({
                ...e,
                Last: true
            }));
        }
        //sƒÅk intervƒÅlu
        runInterval();
    }

    const startRest = () => {   
        //beidz intervalu un maina laiku uz nepieciesamo lai saktu atputas intervalu         
        clearInterval(timer);
        setTimer(null);
        setExerciseStatus("rest");        
        setExerciseInfo(e => ({
            ...e,
            Sets: e.Sets-1,
            Time: e.Rest
        }));
        runInterval();
    }

    const runInterval = () => {
        //sak intervalu
        setTimer(setInterval(()=> {
            setExerciseInfo(e => ({
                ...e,
                Time: e.Time-1
            }));
        }, 1000))
    }

    const stopTimer = () => {
        //pauzes pogai, apstadina intervalu un iztukso to
        clearInterval(timer);
        setTimer(null);
    }

    return(
        <div>
            {complete === true ? 
                <div>
                    <h1 className='center' style={{color: "greenyellow"}}>
                        Congratulations {getAuth().currentUser.displayName}!
                    </h1>
                    <h2 className='center' style={{color: "greenyellow"}}>{exerciseInfo.Name} has been finished!</h2>
                </div>
            :
            <div>
            <div className="timer-header">
                <h1 className='timer'>
                    {exerciseInfo ? 
                    (//Formulas laika iegu≈°anai no skaitƒºa
                        (Math.floor(exerciseInfo.Time/60) < 10 ?
                        '0' + Math.floor(exerciseInfo.Time/60) : Math.floor(exerciseInfo.Time/60))
                     + ':' + 
                     (exerciseInfo.Time%60 < 10 ?
                        '0' + exerciseInfo.Time%60 : exerciseInfo.Time%60)) 
                    : null}
                    </h1>
                <div className="timer-btns">
                    <div className="play" onClick={() => startExercise()}>
                        <FontAwesomeIcon size="lg" icon={faPlay} />
                    </div>
                    <div className="pause" onClick={() => stopTimer()}>
                        <FontAwesomeIcon size="lg" icon={faPause} />
                    </div>
                </div>
                <h4>{exerciseInfo.Sets}</h4>
            </div>
            {workouts.map((workout) => {
                if(exerciseIds.includes(workout._id)){
                    return(
                        <div className={`object-box ${workoutStatus.Running === workout._id ? exerciseStatus : (workoutStatus.Done.includes(workout._id) ? 'done-status' : null)}`}>
                            <h1>{workout.Name}</h1>
                            <p>{workout.Description}</p>
                        </div>
                    );
                }
           })}
           </div>
           }
        </div>
    );
}