import React, {useState, useEffect} from 'react';
//ikonas faili
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHome, faCog, faNewspaper } from "@fortawesome/free-solid-svg-icons";
//darbam ar db
import { getFirestore, collection, onSnapshot } from "firebase/firestore";
//galvenie ekrani
import HomePage from './homePage';
import NewsletterPage from './newsletterPage';
import ProfilePage from './profilePage';
//lietotaja ieguvei
import { getAuth } from "firebase/auth";
//Valodas faili
import * as en from '../translation/en';
import * as lv from '../translation/lv';
import * as ru from '../translation/ru';

export default function Content() {    
    //tulkojumam vajadzƒ´gƒÅ sadƒÅƒºa
    const [userData, setUserData] = useState({});
    const refUser = collection( getFirestore(), "users" );
    const getUserData = () => {
        onSnapshot(refUser, (item) => {
            const items = [];
            item.forEach((doc) => {
                items.push({
                    uid: doc.data().uid,
                    lan: doc.data().lan,
                });
            })
            setUserData(items.find(e => e.uid === getAuth().currentUser.uid));
          });
    }
    const getTranslation = () => {
        switch(userData.lan) {
            case 'lv':
                return lv
            case 'en':
                return en         
            case 'ru':
                return ru
            default :
                return 'undefined'
          }
    }
    //
    const [navActive, setNavActive] = useState("home");
    const [pastState, setPastState] = useState("");
    const [clicked, setClicked] = useState(false);
    
    useEffect(() => {
        //lai nospiezot velreiz kadu no navigacijas pogam atgriestos uzreiz galvenaja ekrana
        if(pastState !== ''){
            setNavActive(pastState)
        }
    }, [pastState])

    useEffect(() => {
        getUserData();
    }, [])

    const getCorrespondingPage = (value) => {
        //atrod nokliskinato stavokli
        switch(value) {
            case 'home':
                return <HomePage clicked={clicked} setClicked={setClicked}/>
            case 'profile':
                return <ProfilePage />         
            case 'newsletter':
                return <NewsletterPage /> 
          }
    }

    const refreshUsingState = (value) => {
        //lai atjaunotu sakuma ekranu
        setPastState('');
        if(pastState === ''){
            setNavActive("");
            setPastState(value);
        }
    }  

    return(
        <div className="container content">
            {getCorrespondingPage(navActive)}
            <ul className="nav bottom-nav">
                <li className="nav-item">
                    <a className={`nav-link ${navActive === "newsletter" ? "active" : null}`} onClick={() => navActive !== "newsletter" ? setNavActive("newsletter") : refreshUsingState("newsletter")} href="#">
                        <div className="text-center pt-1">
                            <FontAwesomeIcon size="lg" icon={faNewspaper} />
                            <p><small>{getTranslation().news}</small></p>
                        </div>
                    </a>
                </li>
                <li className="nav-item">
                    <a className={`nav-link ${navActive === "home" ? "active" : null}`} onClick={() =>  navActive !== "home" ? setNavActive("home") : refreshUsingState("home")} href="#">
                        <div className="text-center pt-1">
                            <FontAwesomeIcon size="lg" icon={faHome} />
                            <p><small>{getTranslation().home}</small></p>
                        </div>
                    </a>
                </li>
                <li className="nav-item">
                    <a className={`nav-link ${navActive === "profile" ? "active" : null}`} onClick={() => navActive !== "profile" ? setNavActive("profile") : refreshUsingState("profile")} href="#">
                        <div className="text-center pt-1">
                            <FontAwesomeIcon size="lg" icon={faCog} />
                            <p><small>{getTranslation().settings}</small></p>
                        </div>
                    </a>
                </li>
            </ul>
        </div>
    );
}