import React, {useState,useEffect} from 'react';
//auth lietotƒÅja ieg≈´≈°anai
import { getAuth } from "firebase/auth";
import { useAuthState } from 'react-firebase-hooks/auth';
//darbam ar db
import { getFirestore, collection, updateDoc, doc, onSnapshot } from "firebase/firestore";

export default function ChangeUserInfo() {
    
    const [user] = useAuthState(getAuth());
    const [userData, setUserData] = useState({});
    const [updatedUser, setUpdatedUser] = useState({
        age: 0,
        weight: 0,
        height: 0,
        activityLevel: '',
    });
    const ref = collection( getFirestore(), "users" );

    useEffect(() => {
        //lietotƒÅju ieguvei, draugiem un lietotaja info, vareju to visu viena pieprasijuma, bet rakstot bija saprotamak divos
        onSnapshot(ref, (item) => {
          const items = [];
          item.forEach((doc) => {
              items.push({
                  _id: doc.id,
                  carbs: 0,
                  protein: 0,
                  fat: 0,
                  uid: doc.data().uid,
                  gender: doc.data().gender,
                  age: doc.data().age,
                  weight: doc.data().weight,
                  height: doc.data().height,
                  activityLevel: doc.data().activityLevel,
                  active: doc.data().active,
                  calories: doc.data().calories,
                  lan: doc.data().lan,
                  friends: doc.data().friends,
              });
          })
          setUserData(items.find(e => e.uid === user.uid));
          setUpdatedUser({
              age: userData.age,
              weight: userData.weight,
              height: userData.height,
              activityLevel: userData.activityLevel,
          })
        });
    }, []);

    const updateData = () => {
        //pievienot visu user info users tabulai
        updateDoc(doc(getFirestore(), "users", userData._id), {
            age: (updatedUser.age ? updatedUser.age : userData.age),
            weight: (updatedUser.weight ? updatedUser.weight : userData.weight),
            height: (updatedUser.height ? updatedUser.height : userData.height),
            activityLevel: (updatedUser.activityLevel ? updatedUser.activityLevel : userData.activityLevel),
        });
    }

    useEffect(() => {
        console.log(userData)
    }, [userData])

      return (
        <div className="center-content">
            Hi {user.displayName}!
            <div className="user-info">
                <div className="user-info-row">
                    <h5>
                        Gender
                    </h5>
                    <div>
                        {userData.gender}
                    </div>
                </div>
                <div className="user-info-row">
                    <h5 className="align-center">
                        Age
                    </h5>
                    <div>
                        <input type="number" placeholder={userData.age} 
                        onChange={num => setUpdatedUser(e => ({
                            ...e,
                            age: num.target.value,
                        }))}>
                        </input>
                    </div>
                </div>
                <div className="user-info-row">
                    <h5 className="align-center">
                        Weight
                    </h5>
                    <div>
                        <input type="number" placeholder={userData.weight}                        
                        onChange={num => setUpdatedUser(e => ({
                            ...e,
                            weight: num.target.value,
                        }))}></input>
                    </div>
                </div>
                <div className="user-info-row">
                <h5 className="align-center">
                        Height
                    </h5>
                    <div>   
                        <input type="number" placeholder={userData.height}
                        onChange={num => setUpdatedUser(e => ({
                            ...e,
                            height: num.target.value,
                        }))}></input>
                    </div>
                </div>
                <div className="user-info-row">
                <h5 className="align-center">
                        Activity level
                    </h5>
                    <div>
                        <select className="select-input" 
                        onChange={num => setUpdatedUser(e => ({
                            ...e,
                            activityLevel: num.target.value,
                        }))}>
                            <option value="" disabled selected>
                                Change activity level
                            </option>
                            <option value="sedentary">Sedentary</option>
                            <option value="lactive">Lightly Active(1-2 days)</option>
                            <option value="mactive">Moderately Active(3-5 days)</option>
                            <option value="vactive">Very Active(6-7 days)</option>
                            <option value="eactive">Extremely Active</option>
                        </select>
                    </div>
                </div>
                <button className="submit-btn" onClick={updateData}>Update</button>
            </div>
        </div>
      );
}